<template lang="html">
<div class="wrap_area2">
	<a href="javascript:void(0)" class="btn_present" @click="geolocationCall"><img src="../assets/images/btn_position.png" alt="현재위치"/></a>
</div>	
</template>

<script lang="js">
  export default  {
    name: 'geo-map',
    props: [],
		computed: {},
		mounted() {},
    data() {
      return {
				map:null,
				mapAreaRef: null,
        fullHeight: 0
      }
		},
		created() {
			_eventBus.$on('mapCover', this.mapCoverHandler);
		},
		methods: {
			initMap:function(mapArea){
				this.mapAreaRef = mapArea
				this.map = new olleh.maps.Map(mapArea.id, {
					zoomControl: false,
					zoomControlOptions: {
						top: 10,
						right: 15
					},
					panControl: false,
					scaleControl: true,
					scaleControlOptions:{
							left: 7, // 기본 14
							bottom:5
					},
					copyrightControl: true,
					copyrightControlOptions:{bottom:9},
					mapTypeControl: false,
					measureControl: false,
					measureControlOptions:{right:15},
          geolocateControl: false,
          trafficControl: false
				});

				var coordCenter = new olleh.maps.UTMK(953828.1032726372, 1952440.2230947923); //958386.063532902, 1941447.5761742294:우면동, 953828.1032726372, 1952440.2230947923:광화문, 965898.9999999991, 1928928.9999999197:분당
				this.map.setCenter(coordCenter);
				this.map.setZoom(11);
				window._map = this.map;
			},

			mapCoverHandler: function(status){
				if(status.wOpenStatus){
					this.mapAreaRef.style.height = "50%";
				}else{
					if(status.resultAreaVisible){
						this.mapAreaRef.style.height = ($(document).height()-99) + "px";
					}else{
						this.mapAreaRef.style.height = "100%";
					}
				}
				window.dispatchEvent(new Event('resize'));
			},

			geolocationCall: function(){
				if (navigator.geolocation) { // GPS를 지원하면
          				navigator.geolocation.getCurrentPosition(
						function(position) {
							var position = new olleh.maps.LatLng(position.coords.latitude, position.coords.longitude);
							_map.panTo(position);
						}, 
						function(error) {
							console.error(error);
						}, 
						{
							enableHighAccuracy: false,
							maximumAge: 0,
							timeout: Infinity
						}
					);
				} else {
						alert('GPS를 지원하지 않습니다');
				}
			}
		}
	}
</script>

<style scoped>
</style>


